
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import PopupPicker from 'c%/PopupPicker.vue'
import SelectEmployee from 'c%/SelectEmployee.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


const emptyObject = {
extra_consume_did: 0,
teach_eid: 0,
lesson_hours: 1,
remark: ''
}

export default{
mixins: [ common, globals, modal ],
directives: {
TransferDom
},
props: {
consumeLessonHour: {
type: [Number,String],
default: 0
},
remainLessonHours: {
type: [Number,String],
default: 0
}
},
components: {
Popup,
Group,
XInput,
XNumber,
XButton,
TransferDom,
Flexbox,
FlexboxItem,
PopupPicker,
SelectEmployee
},
data () {
return {
student_name: '',
teach_eid: 0,
info: util.copy(emptyObject)
}
},
watch: {
'modal$.show':function(val) {
if(val && this.modal$.action == 'add') {
this.info.teach_eid = this.teach_eid
}
}
},
computed: {
style () {
return {
top: this.headerHeight +'px',
height: `calc(100% - ${this.headerHeight}px)`
}
},
consume_options() {
let ret = []
this.dicts('extra_consume').forEach(e => {
ret.push({
value: e.title,
key: e.did
})
})
return ret
}
},
methods: {
ok() {
if(this.info.extra_consume_did == 0) {
this.toast('请选择课消项目','warn')
return
}
if(this.info.lesson_hours == 0) {
this.toast('请输入消耗数量','warn')
return
}
let diff = this.remainLessonHours - this.consumeLessonHour - this.info.lesson_hours
if(diff < 0) {
this.toast('你输入的课时数超过限制','warn')
return
}
if(this.info.teach_eid == 0) {
this.toast('请选择上课老师','warn')
return
}
this.$emit('on-success',this.info)
this.close()
},
cancel() {
this.$emit('on-cancel')
this.close()
}
}
}



