
import Vue from 'vue'
import moment from 'moment'
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import shedule from './shedule.mixin'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import ArrangeStudent from './arrange-student.vue'
import CellContent from '../comment/cell-content.vue'

const emptyObject = {
lesson_type: 0,
teach_eid: 0,
lesson_remark: '',
students: [],
consume_lesson_hour: 0,
consume_lesson_amount: 0,
is_push: 1
}

export default{
mixins: [common,shedule,globals],
components: {
Flexbox,
XButton,
Group,
XInput,
CellContent,
FlexboxItem,
ArrangeStudent
},
data () {
return {
id: this.$route.params.id,
arrange: {},
data: [],
ready: false,
saving: false,
loading: false,
post: util.copy(emptyObject)
}
},
mounted () {
this.init_data()
},
computed: {
allow_debt_att () {
let result = false
if(this.$store.state.gvars.configs&&this.$store.state.gvars.configs.params){
result = this.$store.state.gvars.configs.params.class_attendance.allow_debt_att
}
return result
},
getAttendanceName () {
let arrange = this.arrange,
mapTitle = {1:'一对一',2:'一对多'},
result = ''
if(arrange.lesson_type===0){
result = arrange.one_class!=null?arrange.one_class.class_name:arrange.name
}else{
result = mapTitle[arrange.lesson_type]
}

return result
},
sheduleDate () {
let result = ''
if(this.arrange.int_day){
result = moment(this.$filter('int_date')(this.arrange.int_day)).format('YYYY年MM月DD日')
}
return result
},
lesson () {
let lessons = this.$store.state.gvars.lessons,
lid = this.arrange.lid,
result = {}
if(lessons){
result = lessons.find(l=>l.lid==lid)
}
return result
},
regatt_default_consume() {
return this.$store.state.gvars.configs.params.student_leave.regatt_default_consume
},
disabledOK() {
if(this.not_arrived_int_day()){
return true
}
return false
}
},
methods: {
cancelResetMoney(index) {
var students = [].concat(this.post.students);
students[index].is_reset = false;
Object.assign(this.post,{
students: students
})
},
resetMoney(index) {
var students = [].concat(this.post.students);
students[index].is_reset = true;
Object.assign(this.post,{
students: students
})
},
togglePush () {
this.post.is_push = this.post.is_push == 1 ? 0 : 1
},
redoAttendance () {
this.loading = true
this.$rest('class_attendances')
.delete(this.arrange.class_attendance.catt_id)
.success(response=>{
this.loading = false
this.toast('考勤撤销成功','success')
this.init_data()
}).error(response=>{
this.loading = false
this.toast(response.body.message||'撤销失败','warn')
})
},
handleSubmit () {
if(this.saving){
return
}

if(!this.post.students.length){
this.toast('没有可考勤学员')
return false
}

this.saving = true

this.$rest('course_arranges')
.add_url_param(this.id,'regatts')
.post(this.post)
.success(data=>{
this.saving = false
this.toast('考勤成功','success')
this.$router.go(-1)
}).error(response=>{
this.saving = false
this.toast(response.body.message||'考勤失败','warn')
})
},
not_arrived_int_day(){
let int_day = util.today_int_day()
if(this.arrange.int_day > int_day){
return true
}
return false
},
init_data() {
let ca_id = this.$route.params.id
this.showLoading()
this.$rest('course_arranges?with=lesson,class_attendance,one_class,course_prepare&ca_id='+ca_id)
.get()
.success(data=>{
this.arrange = data.list[0]
this.setPost()
this.getStudents()
this.setTitle()
this.ready = true
})
.error(res => {
this.toast(res.body.message,'warn')
this.hideLoading()
})
},
setPost () {
this.post.teach_eid = this.arrange.teach_eid
this.post.lesson_type = this.arrange.lesson_type
this.post.consume_lesson_hour = this.arrange.consume_lesson_hour
this.post.consume_lesson_amount = this.arrange.consume_lesson_amount
this.post.lesson_remark = this.arrange.class_attendance?this.arrange.class_attendance.lesson_remark:''
},
setTitle () {
if(this.arrange.is_attendance<2){
util.title('添加考勤',this.$store.state.sys_name)
}else{
util.title('考勤记录',this.$store.state.sys_name)
}
},
getStudents () {
this.$rest('course_arranges/'+this.id+'/attobjects')
.get()
.success(data=>{
let student_remain_lesson_hours = 0
this.post.students = data
this.consumeChange()
this.hideLoading()
})
.error(res => {
this.toast(res.body.message,'warn')
this.hideLoading()
})
},
consumeChange () {
let consume_lesson_hour = Number(this.post.consume_lesson_hour),
consume_lesson_amount = Number(this.post.consume_lesson_amount),
student_remain_lesson_hours = 0,
student_remain_money = 0
this.post.students.forEach(item=>{
item.is_reset = false;
item.reset_val = 0;
if(!this.allow_debt_att){
if(_.isEmpty(item.student)){
return
}

if(item.student.status != 1){
this.$set(item,'_disabled',true)
return
}

if(item.student.student_lesson.price_type == 3){

if(item.student.student_lesson.is_expired || item.student.student_lesson.remain_days == 0){
this.$set(item,'_disabled',true)
return
}
}else{
if(this.arrange.consume_source_type == 1)
{

if(item.student.student_lesson){
student_remain_lesson_hours = Number(item.student.student_lesson.remain_lesson_hours)
}

if(student_remain_lesson_hours < consume_lesson_hour){
this.$set(item,'_disabled',true)
return
}
}
}

}

if(!_.isEmpty(item.student)
&& this.arrange.consume_source_type == 2
) {

if(Number(item.student.money) < consume_lesson_amount){
this.$set(item,'_disabled',true)
return
}
}

if(item.is_leave == 1){
this.$set(item,'_disabled',true)
if(this.regatt_default_consume) {
this.$set(item,'is_consume',1)
}
}else{
this.$set(item,'_disabled',false)
}
})
}
}
}



