
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import Popover from 'vux/src/components/popover/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import Tag from 'c%/Tag.vue'
import ExtraConsumeInfo from './extra-consume-popup.vue'

export default{
mixins: [ common ],
components: {
Tag,
XCircle,
Popover,
Icon,
ExtraConsumeInfo
},
props: {
arrange: {
type: Object,
default: ()=>{
return {}
}
},
list: {
type: Array,
default: ()=>{
return []
}
},
disabled: {
type: Boolean,
default: false,
},
teachEid: {
type: Number,
default: 0
},
consumeLessonHour: {
type: [Number,String],
default: 0
}
},
data () {
return {
all: {
is_in: false,
is_consume: false,
is_leave: false
},
status_list: {
1:{value:1,label:'正常'},
20:{value:20,label:'停课'},
30:{value:30,label:'休学'},
50:{value:50,label:'结课'},
90:{value:90,label:'退学'},
100:{value:100,label:'封存'}
},
currentRemain: 0
}
},
computed: {
is_attendance_by_hours () {
return this.arrange.lid == 0 || (this.arrange.lid > 0 && this.arrange.lesson && this.arrange.lesson.price_type !== 3)
},
is_makeup_class_ca(){
if(this.arrange && this.arrange.one_class && this.arrange.one_class.class_type == 1){
return true
}
return false
},
regatt_default_consume() {
return this.$store.state.gvars.configs.params.student_leave.regatt_default_consume
},
enable_extra_consume() {
return this.$store.state.gvars.configs.params.class_attendance.enable_extra_consume
}
},
methods:{
cancelResetMoney(index) {
this.$emit('on-cancel-reset',index);
},
resetMoney(item,index) {
this.$emit('on-reset',index);
},
leaveReasonList (item) {
return this.dicts('leave_reason');
},
setStudentTag (item) {
let obj = {
color: 'blue',
label: '班'
}
if(item.is_trial==1){
obj.color = 'yellow'
obj.label = '试'
}
if(item.is_makeup==1){
obj.color = 'purple'
obj.label = '补'
}
return obj
},
total (type) {
let list = this.list||[],
length = 0
if(type=='all'){
length = list.length
}else{
length = list.filter(l=>l[type]==1).length
}
return length
},
checkAll (field) {
if(!this.disabled){
this.all[field] = !this.all[field]
let val = this.all[field]

if(field=='is_leave'){
if(val) {
this.all.is_in = false
if(this.regatt_default_consume) {
this.all.is_consume = true
}
}else{
this.all.is_consume = false
}
}
if(field=='is_in'){
if(val) {
this.all.is_leave = false
}
this.all.is_consume = val
}
if(field=='is_consume') {
this.all.is_in = val
}

this.list.forEach(l=>{
if(!l.is_attendance && !l._disabled){
if(
(l.sid > 0
&& l.student.status == 1)
|| l.cu_id > 0
){

if(
!_.isEmpty(l.student.student_lesson)
&& l.student.student_lesson.price_type == 3
&& (field == 'is_leave'
|| field == 'is_consume')
|| (field == 'is_consume' && l.is_trial)
){
return
}
l[field] = 	this.all[field]?1:0
this.itemChange(l[field],l,field)
}
}
})
}
},
itemStatusChange (item,field) {
if(
(field != 'is_consume' && item.is_attendance==0 && !this.disabled && !item._disabled)
||
(field == 'is_consume' && item.is_attendance == 0 && item.student && item.student.status == 1)
){
item[field] = (item[field]==0||item[field]==-1)?1:0
this.itemChange(item[field],item,field)
}

},
itemChange (val,item,field) {
if(field=='is_leave'){
if(val) {
item.is_in = 0
if(this.regatt_default_consume) { // 请假默认扣课时
item.is_consume = 1
}else{
item.is_consume = 0
}
}else{
item.is_in = -1
item.is_consume = 0
}
}
else if(field=='is_consume') {
if(val) {
if(item.is_leave) {
item.is_in = 0
this.all.is_in = false
}else{
item.is_in = 1
}
}else{
if(!item.is_leave) {
item.is_in = -1
}
}
}
else if(field=='is_in'){
if(val) {

if(!item.is_trial) {
item.is_consume = 1
}
item.is_leave = 0
item.remark = ''
}else{
item.is_consume = 0
item.is_in = -1
}
}
},
setStudentInfo (item,field) {
let obj = {}
if(item.sid > 0){
obj = item.student||{}
obj.name = item.student?item.student.student_name:''
}else{
obj = item.customer||{}
}

return obj
},
setStudentExtraConsume(item) {
if(
this.enable_extra_consume
&& item.is_attendance==0
&& !item._disabled
) {
this.current_sid = item.sid
this.currentRemain = item.student.student_lesson.remain_lesson_hours
if(item.extra_consume) {
this.$refs.extra_consume
.set('student_name',item.student.student_name)
.set('info',item.extra_consume)
.show('额外课消','edit')
}else{
this.$refs.extra_consume
.set('teach_eid',this.teachEid)
.set('student_name',item.student.student_name)
.show('额外课消','add')
}
}
},
setExtraConsume(info) {
let index = this.list.findIndex(item => item.sid == this.current_sid)
let item = util.copy(this.list[index])
item.extra_consume = info
this.$set(this.list,index,item)
},
cancelExtraConsume() {
let index = this.list.findIndex(item => item.sid == this.current_sid)
let item = util.copy(this.list[index])
delete item['extra_consume']
this.$set(this.list,index,item)
}
}
}



